.sw-FormSelect {
  position: relative;
  /* Firefox Hack */
  /* IE10以上 */
  /* IE9ではデフォルトの矢印アイコンを表示する。 */
}

.sw-FormSelect:before {
  content: "";
  position: absolute;
  z-index: -1;
  right: 0;
  bottom: 0;
  left: 0;
  width: 99%;
  width: calc(100% - 6px);
  margin: auto;
  box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.05);
}

.sw-FormSelect:after {
  content: "";
  position: absolute;
  top: 0;
  right: 1em;
  bottom: 0;
  width: 1em;
  height: 1em;
  margin: auto;
  background-image: url("/assets/img/SiteWide/Icon/FormSelect.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  pointer-events: none;
}

.sw-FormSelect.is-disabled:after {
  opacity: 0.5;
}

.sw-FormSelect > select {
  width: 100%;
  height: 2.5em;
  padding: 0 3em 0 1em;
